<template lang="html">
  <div class="">
    <div class="ui teal dividing header">
      <i class="filter icon"></i>
      <div class="content">
        Puit <div class="sub header">Détail puit "Bangourin - puit 1"</div><i v-if="sending" class="spinner large loading icon" style="position:absolute;top:10px; right:10px;"></i>
      </div>
    </div>
    <div class="ui two aligned grid stackable fluid" style="padding-top:20px;">
      <div class="ten wide column">
        <div id="chartdiv"></div>
      </div>
      <div class="six wide column">
        <h3>Ajout d'un echantillon</h3>
        <form class="ui form">
          <div class="field">
            <label>Code</label>
            <input type="text" v-model="echantillon.code">
          </div>
          <div class="field">
            <label>Profondeur debut</label>
            <input type="number" v-model="echantillon.pfd_debut">
          </div>
          <div class="field">
            <label>Profondeur fin</label>
            <input type="number" v-model="echantillon.pfd_fin">
          </div>
          <div class="field">
            <label>Date</label>
            <input type="date" v-model="echantillon.date">
          </div>
          <div class="field">
            <label>Couleur</label>
            <input type="text" v-model="echantillon.couleur">
          </div>
          <div class="field">
            <label>structure</label>
            <input type="text" v-model="echantillon.structure">
          </div>
          <div class="field">
            <label>texture</label>
            <input type="text" v-model="echantillon.texture">
          </div>
          <div class="field">
            <label>Autre information</label>
            <textarea rows="2" v-model="echantillon.autre_info"></textarea>
          </div>
          <button class="ui primary button" @click.prevent="sendData" type="submit">Enregistrer</button>
        </form>
      </div>
    </div>
    <div class="ui fullscreen modal detailEchantillon">
      <div class="header">Détail Echantillon {{detailEchantillon.code}}</div>
      <div class="content">
        <div class="ui grid stackable fluid">
          <div class="five wide column">

          </div>
          <div class="five wide right column">
            <h3>Ajout resultat d'analyse</h3>
            <form class="ui form">
              <div class="field" :class="{error: error.technique}">
                <label>Nom Technique</label>
                <input type="text" v-model="resultatAnalyse.technique" placeholder="Nom Technique">
              </div>
              <div class="field" :class="{error: error.protocole}">
                <label>Protocole</label>
                <input type="text" v-model="resultatAnalyse.protocole" placeholder="Protocole">
              </div>
              <div class="field" :class="{error: error.appareillage}">
                <label>Appareillage</label>
                <input type="text" v-model="resultatAnalyse.appareillage" placeholder="Appareillage">
              </div>
              <div class="field" :class="{error: error.autreInfo}">
                <label>Autre Info</label>
                <input type="text" v-model="resultatAnalyse.autreInfo" placeholder="Autre Information">
              </div>
              <div class="field">
                <label>Laboratoire</label>
                <select class="ui fluid search dropdown" name="card[expire-month]">
                  <option :value="labo.id" v-for="labo in laboratoires">{{labo.nom}}</option>
                </select>
              </div>
              <div class="field">
                <label>Analyse</label>
                <select class="ui fluid search dropdown" name="card[expire-month]">
                  <option value="1">test</option>
                  <option value="2">ui</option>
                </select>
              </div>
              <button class="ui primary button" @click.prevent="sendData" type="submit">Enregistrer</button>
            </form>
          </div>
          <div class="six wide right column">
            fhkgdf
          </div>
        </div>
      </div>
      <div class="actions">
         <div class="ui button" @click.prevent="closeDetail()">Femer</div>
      </div>
    </div>
  </div>
</template>

<script>
import _ from 'lodash'
import {siteShowUrl,typeAnalyseAddUrl,echantillonUrl,laboratoireUrl,apiDomain, getHeader} from '../../../../config'
import toastr from '../../../../assets/toastr.min.js'
import AmCharts from 'amcharts'
var randomColor = require('random-color')

export default {
  data() {
    return {
      sending: false,
      echantillon: {
        description: '',
        idpuit: null,
        code: '',
        pfd_debut: '',
        pfd_fin: '',
        date: '',
        couleur: '',
        structure: '',
        autre_info: '',
      },
      detailEchantillon: {
        description: '',
        idpuit: null,
        code: '',
        pfd_debut: '',
        pfd_fin: '',
        date: '',
        couleur: '',
        structure: '',
        autre_info: '',
      },
      listeEchantillon : [],
      laboratoires : [],
      typeAnalyses : [],
      resultatAnalyse:{
        technique:"",
        protocole:"",
        appareillage:"",
        autreInfo:""
      },
      error:{
        technique:false,
        protocole:false,
        appareillage:false,
        autreInfo:false
      }
    };
  },
  components: {
  },
  methods: {
    showEchantillonInfo (echantillon){
      this.detailEchantillon = echantillon
      $('.ui.modal.detailEchantillon')
        .modal('show')
      ;
    },
    closeDetail () {
      $('.ui.modal.detailEchantillon')
        .modal('hide')
      ;
    },
    check(){
      if(typeof window.AmCharts !=='undefined'){
        this.showGraph()
      }else{
        setTimeout(this.check,1000);
      }
    },
    getEchantillon(){
      this.$http.get(echantillonUrl,{headers: getHeader()}).then(data => {
          this.listeEchantillon =  data.body;
          this.check();
      },data => {
        this.sending = false
        toastr.error("une erreur serveur c'est produit lors de l'ajout du site")
      })
    },
    sendData(){
      toastr.options = {"newestOnTop": true,"positionClass": "toast-bottom-right"}
      this.echantillon.idpuit = this.$route.params.idpuit
      this.sending = true
      this.$http.post(echantillonUrl,this.echantillon,{headers: getHeader()}).then(data => {
          this.sending = false
          this.echantillon.description = ''
          this.echantillon.code = ''
          this.echantillon.pfd_debut = ''
          this.echantillon.pfd_fin = ''
          this.echantillon.date = ''
          this.echantillon.couleur = ''
          this.echantillon.structure = ''
          this.echantillon.autre_info = ''
          toastr.success("echantillon ajouter avec success")
          this.getEchantillon();
      },data => {
        this.sending = false
        toastr.error("une erreur serveur c'est produit lors de l'ajout du site")
      })
    },
    showGraph () {
      let data = [];
      _.forEach(this.listeEchantillon, (value) => {
          data.push({
            "name": value.code,
            "profondeur_debut": value.pfd_debut,
            "profondeur_fin": value.pfd_fin,
            "data": value,
            "color": randomColor(0.99, 0.99).hexString()
          })
      })
      var chart = window.AmCharts.makeChart("chartdiv", {
      	"theme": "light",
          "type": "serial",
          "dataProvider": data,
          "valueAxes": [{
              "axisAlpha": 0,
              "gridAlpha": 0.1
          }],
          "startDuration": 1,
          "graphs": [{
              "balloonText": "<b>[[category]]</b><br>profondeur debut [[profondeur_debut]]<br>profondeur fin [[profondeur_fin]]",
              "colorField": "color",
              "fillAlphas": 0.8,
              "lineAlpha": 0,
              "openField": "profondeur_debut",
              "type": "column",
              "valueField": "profondeur_fin"
          }],
          "rotate": true,
          "columnWidth": 1,
          "categoryField": "name",
          "categoryAxis": {
              "gridPosition": "start",
              "axisAlpha": 0,
              "gridAlpha": 0.1,
              "position": "left"
          },
          "export": {
          	"enabled": true
           }
      });
      chart.addListener("clickGraphItem", (event) => {
        this.showEchantillonInfo(event.item.dataContext.data);
      });
    }
  },
  mounted(){
    this.$http.get(typeAnalyseAddUrl,{headers: getHeader()}).then(data => {
        this.typeAnalyses =  data.body;
        console.log(data.body);
    },data => {
      this.sending = false
      toastr.error("Impossible de joindre le serveur")
    })
    this.$http.get(laboratoireUrl,{headers: getHeader()}).then(data => {
      this.laboratoires = data.body
      this.data_loading = false
    },data => {
      this.data_loading = false
      toastr.error("Impossible de joindre le serveur")
      this.data_loading = false
    })
    this.getEchantillon();
  },
  computed: {

  }
};
</script>

<style lang="css">
  #chartdiv {
    width		: auto;
    height		: 400px;
    font-size	: 11px;
  }
</style>



